<template>
  <view class="order-detail-container">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="nav-bar">
      <view class="nav-back" @click="goBack">
        <text class="iconfont">‚Äπ</text>
      </view>
      <view class="nav-title">ËÆ¢ÂçïËØ¶ÊÉÖ</view>
      <view class="nav-action" @click="showMoreMenu">
        <text class="iconfont">‚ãØ</text>
      </view>
    </view>

    <!-- ËÆ¢ÂçïÂÜÖÂÆπ -->
    <scroll-view class="detail-content" scroll-y>
      <!-- Âú∞ÂõæÂ±ïÁ§∫ (‰ªÖÂú®ÈÖçÈÄÅ‰∏≠„ÄÅÂæÖÊé•ÂçïÊàñÂ∑≤ÂÆåÊàê‰∏îÊúâÂùêÊ†áÊó∂ÊòæÁ§∫) -->
      <view class="map-section" v-if="orderInfo && (orderStatus === 1 || orderStatus === 2 || orderStatus === 3)">
        <map 
          id="orderMap"
          class="order-map" 
          :latitude="mapCenter.latitude" 
          :longitude="mapCenter.longitude" 
          :markers="markers"
          :polyline="polyline"
          :include-points="markers"
          show-location
        ></map>
      </view>

      <!-- ËÆ¢ÂçïÁä∂ÊÄÅÂç°Áâá -->
      <view class="status-card" :class="'status-' + orderStatus">
        <view class="status-icon">
          <text>{{ getStatusIcon(orderStatus) }}</text>
        </view>
        <view class="status-info">
          <text class="status-title">{{ getStatusTitle(orderStatus) }}</text>
          <text class="status-desc" v-if="orderStatus !== 0 || countdown <= 0">{{ getStatusDesc(orderStatus) }}</text>
          <!-- ÂæÖÊîØ‰ªòÁä∂ÊÄÅÊòæÁ§∫ÂÄíËÆ°Êó∂ -->
          <view class="countdown-inline" v-if="orderStatus === 0 && countdown > 0">
            <text class="countdown-icon">‚è±Ô∏è</text>
            <text class="countdown-text">ÊîØ‰ªòÂâ©‰ΩôÊó∂Èó¥Ôºö{{ formatCountdown(countdown) }}</text>
          </view>
          <text class="status-desc expired" v-if="orderStatus === 0 && countdown === 0">ËÆ¢ÂçïÂ∑≤Ë∂ÖÊó∂</text>
        </view>
      </view>

      <!-- ÈÖçÈÄÅËøõÂ∫¶ -->
      <view class="progress-section" v-if="orderInfo && orderInfo.serviceType">
        <view class="section-title">ÈÖçÈÄÅËøõÂ∫¶</view>
        <view class="progress-timeline">
          <view class="timeline-item" :class="{ active: orderStatus >= 1 }">
            <view class="timeline-dot"></view>
            <view class="timeline-content">
              <text class="timeline-title">ËÆ¢ÂçïÂ∑≤ÂàõÂª∫</text>
              <text class="timeline-time">{{ formatTime(orderInfo.createTime) }}</text>
            </view>
          </view>
          <view class="timeline-item" :class="{ active: orderStatus >= 2 }">
            <view class="timeline-dot"></view>
            <view class="timeline-content">
              <text class="timeline-title">È™ëÊâãÂ∑≤Êé•Âçï</text>
              <text class="timeline-time">{{ orderInfo.acceptTime ? formatTime(orderInfo.acceptTime) : 'Á≠âÂæÖÊé•Âçï' }}</text>
            </view>
          </view>
          <view class="timeline-item" :class="{ active: orderStatus >= 3 }">
            <view class="timeline-dot"></view>
            <view class="timeline-content">
              <text class="timeline-title">ÈÖçÈÄÅ‰∏≠</text>
              <text class="timeline-time">{{ orderInfo.deliveryTime ? formatTime(orderInfo.deliveryTime) : 'ÈÖçÈÄÅ‰∏≠' }}</text>
            </view>
          </view>
          <view class="timeline-item" :class="{ active: orderStatus >= 4 }">
            <view class="timeline-dot"></view>
            <view class="timeline-content">
              <text class="timeline-title">Â∑≤ÂÆåÊàê</text>
              <text class="timeline-time">{{ orderInfo.completeTime ? formatTime(orderInfo.completeTime) : 'Á≠âÂæÖÂÆåÊàê' }}</text>
            </view>
          </view>
        </view>
      </view>

      <!-- ËÆ¢Âçï‰ø°ÊÅØ -->
      <view class="info-section" v-if="orderInfo">
        <view class="section-title">ËÆ¢Âçï‰ø°ÊÅØ</view>
        <view class="info-list">
          <view class="info-item">
            <text class="info-label">ËÆ¢ÂçïÁºñÂè∑</text>
            <view class="info-value-row">
              <text class="info-value">{{ orderInfo.orderNo || '-' }}</text>
              <text class="btn-copy" @click="copyOrderNo">Â§çÂà∂</text>
            </view>
          </view>
          <view class="info-item">
            <text class="info-label">ÊúçÂä°Á±ªÂûã</text>
            <text class="info-value">{{ getServiceTypeName(orderInfo.serviceType) }}</text>
          </view>
          <view class="info-item">
            <text class="info-label">Áâ©ÂìÅ‰ø°ÊÅØ</text>
            <text class="info-value">{{ orderInfo.goodsInfo || '-' }}</text>
          </view>
          <view class="info-item" v-if="orderInfo.remark">
            <text class="info-label">Â§áÊ≥®</text>
            <text class="info-value">{{ orderInfo.remark }}</text>
          </view>
          <view class="info-item">
            <text class="info-label">‰∏ãÂçïÊó∂Èó¥</text>
            <text class="info-value">{{ formatTime(orderInfo.createTime) }}</text>
          </view>
        </view>
      </view>

      <!-- Âú∞ÂùÄ‰ø°ÊÅØ -->
      <view class="address-section" v-if="orderInfo">
        <view class="section-title">Âú∞ÂùÄ‰ø°ÊÅØ</view>
        <view class="address-list">
          <!-- Âèñ‰ª∂Âú∞ÂùÄ -->
          <view class="address-item">
            <view class="address-icon pickup">
              <text>üìç</text>
            </view>
            <view class="address-info">
              <view class="address-header">
                <text class="address-tag">Âèñ‰ª∂Âú∞ÂùÄ</text>
              </view>
              <view class="address-detail">{{ orderInfo.addressInfo?.pickupAddress || '-' }}</view>
            </view>
          </view>

          <!-- ÈÄÅËææÂú∞ÂùÄ -->
          <view class="address-item">
            <view class="address-icon delivery">
              <text>üéØ</text>
            </view>
            <view class="address-info">
              <view class="address-header">
                <text class="address-tag">ÈÄÅËææÂú∞ÂùÄ</text>
                <text class="contact-info">{{ orderInfo.deliveryName }} {{ orderInfo.deliveryPhone }}</text>
              </view>
              <view class="address-detail">{{ orderInfo.deliveryAddress || '-' }}</view>
            </view>
          </view>
        </view>
      </view>

      <!-- È™ëÊâã‰ø°ÊÅØ -->
      <view class="rider-section" v-if="riderInfo">
        <view class="section-title">È™ëÊâã‰ø°ÊÅØ</view>
        <view class="rider-card">
          <view class="rider-avatar">
            <text>{{ riderInfo.realName ? riderInfo.realName.substring(0, 1) : 'È™ë' }}</text>
          </view>
          <view class="rider-info">
            <text class="rider-name">{{ riderInfo.realName || 'È™ëÊâã' }}</text>
            <text class="rider-rating" v-if="riderInfo.averageRating">‚≠ê {{ riderInfo.averageRating.toFixed(1) }}</text>
          </view>
          <view class="rider-actions">
            <button class="btn-call" @click="callRider">
              <text class="btn-icon">üìû</text>
              <text>ËÅîÁ≥ª</text>
            </button>
          </view>
        </view>
      </view>

      <!-- Ë¥πÁî®ÊòéÁªÜ -->
      <view class="cost-section" v-if="orderInfo">
        <view class="section-title">Ë¥πÁî®ÊòéÁªÜ</view>
        <view class="cost-list">
          <view class="cost-item">
            <text class="cost-label">ÂïÜÂìÅ/ÊúçÂä°Ë¥π</text>
            <text class="cost-value">¬•{{ orderInfo.goodsAmount || '0.00' }}</text>
          </view>
          <view class="cost-item">
            <text class="cost-label">ÈÖçÈÄÅË¥π</text>
            <text class="cost-value">¬•{{ orderInfo.deliveryFee || '0.00' }}</text>
          </view>
          <view class="cost-item total">
            <text class="cost-label">ÂÆû‰ªòÊ¨æ</text>
            <text class="cost-value">¬•{{ orderInfo.totalAmount || '0.00' }}</text>
          </view>
        </view>
      </view>
    </scroll-view>

    <!-- Â∫ïÈÉ®Êìç‰ΩúÊ†è -->
    <view class="bottom-bar" v-if="orderInfo && shouldShowBottomBar">
      <!-- ÂæÖÊîØ‰ªòÁä∂ÊÄÅ -->
      <view class="action-buttons" v-if="orderStatus === 0">
        <button class="btn-cancel" @click="cancelOrder">ÂèñÊ∂àËÆ¢Âçï</button>
        <button class="btn-primary" @click="goToPay">ÂéªÊîØ‰ªò</button>
      </view>

      <!-- ÂæÖÊé•ÂçïÁä∂ÊÄÅ -->
      <view class="action-buttons" v-if="orderStatus === 1">
        <button class="btn-cancel" @click="cancelOrder">ÂèñÊ∂àËÆ¢Âçï</button>
      </view>

      <!-- ÈÖçÈÄÅ‰∏≠Áä∂ÊÄÅ -->
      <view class="action-buttons" v-if="orderStatus === 2">
        <button class="btn-primary" @click="contactRider">ËÅîÁ≥ªÈ™ëÊâã</button>
      </view>

      <!-- Â∑≤ÂÆåÊàêÁä∂ÊÄÅ - Êú™ËØÑ‰ª∑ -->
      <view class="action-buttons" v-if="orderStatus === 3 && (orderInfo.rating === null || orderInfo.rating === undefined)">
        <button class="btn-primary" @click="goToEvaluate">ÂéªËØÑ‰ª∑</button>
      </view>

      <!-- Â∑≤ÂÆåÊàêÁä∂ÊÄÅ - Â∑≤ËØÑ‰ª∑ -->
      <view class="action-buttons" v-if="orderStatus === 3 && (orderInfo.rating !== null && orderInfo.rating !== undefined)">
        <button class="btn-primary" @click="viewEvaluation">Êü•ÁúãËØÑ‰ª∑</button>
      </view>

      <!-- Â∑≤ÂèñÊ∂àÁä∂ÊÄÅ -->
      <view class="action-buttons" v-if="orderStatus === 4">
        <button class="btn-cancel" @click="deleteOrder">Âà†Èô§ËÆ¢Âçï</button>
        <button class="btn-primary" @click="reorder">ÂÜçÊù•‰∏ÄÂçï</button>
      </view>
    </view>
  </view>
</template>

<script>
import { getOrderDetail, cancelOrder, reportLocation, getRiderLocation } from '@/api/order.js'
import { getBaiduLocation } from '@/utils/location.js'
import { getUserInfo } from '@/utils/token.js'
import riderTracker from '@/utils/tracker.js'

export default {
  data() {
    return {
      orderId: null,
      orderInfo: null,
      riderInfo: null,
      orderStatus: 0, // 0-ÂæÖÊîØ‰ªò, 1-ÂæÖÊé•Âçï, 2-ÈÖçÈÄÅ‰∏≠, 3-Â∑≤ÂÆåÊàê, 4-Â∑≤ÂèñÊ∂à
      countdown: -1, // ÂÄíËÆ°Êó∂ÁßíÊï∞
      countdownTimer: null, // ÂÄíËÆ°Êó∂ÂÆöÊó∂Âô®
      
      // Âú∞ÂõæÁõ∏ÂÖ≥Êï∞ÊçÆ
      mapCenter: { latitude: 22.817, longitude: 108.366 }, // ÈªòËÆ§ÂçóÂÆÅ
      markers: [],
      polyline: [],
      trackingTimer: null, // ‰ΩçÁΩÆËøΩË∏™ÂÆöÊó∂Âô® (ÊãâÂèñÊàñÊä•)
    }
  },

  computed: {
    /**
     * ÊòØÂê¶ÊòæÁ§∫Â∫ïÈÉ®Êìç‰ΩúÊ†è
     */
    shouldShowBottomBar() {
      // Âè™Êúâ0-4ÁöÑÁä∂ÊÄÅÊâçÊòæÁ§∫Â∫ïÈÉ®Ê†è
      return this.orderStatus >= 0 && this.orderStatus <= 4
    }
  },

  onLoad(options) {
    if (options.id) {
      this.orderId = options.id
      this.loadOrderDetail()
    }
  },

  onShow() {
    // ‰ªéÊîØ‰ªòÈ°µËøîÂõûÊó∂Âà∑Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ
    if (this.orderId) {
      this.loadOrderDetail()
    }
  },

  onUnload() {
    // È°µÈù¢Âç∏ËΩΩÊó∂Ê∏ÖÈô§ÂÆöÊó∂Âô®
    this.stopCountdown()
    this.stopTracking()
  },

  methods: {
    /**
     * Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
     */
    async loadOrderDetail() {
      try {
        uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' })

        const res = await getOrderDetail(this.orderId)

        uni.hideLoading()

        if (res.code === 200 && res.data) {
          this.orderInfo = res.data
          this.orderStatus = res.data.status || 0

          // Â¶ÇÊûúÊúâÈ™ëÊâã‰ø°ÊÅØ
          if (res.data.runnerInfo) {
            this.riderInfo = res.data.runnerInfo
          }

          console.log('‚úÖ ËÆ¢ÂçïËØ¶ÊÉÖÂä†ËΩΩÊàêÂäü:', this.orderInfo)
          console.log('üìä ËØÑ‰ª∑Áä∂ÊÄÅÊ£ÄÊü•:', {
            rating: this.orderInfo.rating,
            hasRating: !!this.orderInfo.rating,
            status: this.orderStatus
          })

          // Â¶ÇÊûúÊòØÂæÖÊîØ‰ªòÁä∂ÊÄÅÔºåÂêØÂä®ÂÄíËÆ°Êó∂
          if (this.orderStatus === 0) {
            if (res.data.countdown && res.data.countdown > 0) {
              this.startCountdown(res.data.countdown)
            } else {
              // ËÆ¢ÂçïÂèØËÉΩÂ∑≤Ë∂ÖÊó∂
              this.countdown = 0
            }
          }

          // Â§ÑÁêÜÂú∞ÂõæÊ†áÊ≥®
          this.initMapMarkers()

          // Â§ÑÁêÜ‰ΩçÁΩÆËøΩË∏™ÈÄªËæë
          this.handleTracking()
        } else {
          uni.showToast({
            title: res.message || 'Âä†ËΩΩÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        uni.hideLoading()
        console.error('‚ùå Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
          icon: 'none'
        })
      }
    },

    /**
     * Ëé∑ÂèñÁä∂ÊÄÅÂõæÊ†á
     */
    getStatusIcon(status) {
      const icons = {
        0: 'üí≥', // ÂæÖÊîØ‰ªò
        1: '‚è∞', // ÂæÖÊé•Âçï
        2: 'üöö', // ÈÖçÈÄÅ‰∏≠
        3: '‚úÖ', // Â∑≤ÂÆåÊàê
        4: '‚ùå'  // Â∑≤ÂèñÊ∂à
      }
      return icons[status] || 'üì¶'
    },

    /**
     * Ëé∑ÂèñÁä∂ÊÄÅÊ†áÈ¢ò
     */
    getStatusTitle(status) {
      const titles = {
        0: 'ÂæÖÊîØ‰ªò',
        1: 'Á≠âÂæÖÊé•Âçï',
        2: 'ÈÖçÈÄÅ‰∏≠',
        3: 'Â∑≤ÂÆåÊàê',
        4: 'Â∑≤ÂèñÊ∂à'
      }
      return titles[status] || 'Êú™Áü•Áä∂ÊÄÅ'
    },

    /**
     * Ëé∑ÂèñÁä∂ÊÄÅÊèèËø∞
     */
    getStatusDesc(status) {
      const descs = {
        0: 'ËØ∑Âú®30ÂàÜÈíüÂÜÖÂÆåÊàêÊîØ‰ªò',
        1: 'Ê≠£Âú®‰∏∫ÊÇ®ÂåπÈÖçÂêàÈÄÇÁöÑÈ™ëÊâã',
        2: 'È™ëÊâãÊ≠£Âú®ÁÅ´ÈÄüÈÖçÈÄÅ‰∏≠',
        3: 'ËÆ¢ÂçïÂ∑≤ÂÆåÊàê',
        4: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à'
      }
      return descs[status] || ''
    },

    /**
     * Ëé∑ÂèñÊúçÂä°Á±ªÂûãÂêçÁß∞
     */
    getServiceTypeName(type) {
      const types = {
        1: 'Â∏ÆÊàë‰π∞',
        2: 'Â∏ÆÊàëÈÄÅ',
        3: 'Â∏ÆÊàëÂèñ',
        4: 'ÂÖ®ËÉΩ'
      }
      return types[type] || '-'
    },

    /**
     * Ê†ºÂºèÂåñÊó∂Èó¥
     */
    formatTime(time) {
      if (!time) return '-'
      const date = new Date(time)
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hours = String(date.getHours()).padStart(2, '0')
      const minutes = String(date.getMinutes()).padStart(2, '0')
      return `${month}-${day} ${hours}:${minutes}`
    },

    /**
     * Â§çÂà∂ËÆ¢ÂçïÂè∑
     */
    copyOrderNo() {
      if (!this.orderInfo || !this.orderInfo.orderNo) return

      uni.setClipboardData({
        data: this.orderInfo.orderNo,
        success: () => {
          uni.showToast({
            title: 'ËÆ¢ÂçïÂè∑Â∑≤Â§çÂà∂',
            icon: 'success'
          })
        }
      })
    },

    /**
     * ËÅîÁ≥ªÈ™ëÊâã
     */
    contactRider() {
      if (!this.riderInfo) return

      uni.showActionSheet({
        itemList: ['Êã®ÊâìÁîµËØù'],
        success: (res) => {
          if (res.tapIndex === 0) {
            this.callRider()
          }
        }
      })
    },

    /**
     * Êã®ÊâìÈ™ëÊâãÁîµËØù
     */
    callRider() {
      if (!this.riderInfo || !this.riderInfo.phone) {
        uni.showToast({
          title: 'ÊöÇÊó†ËÅîÁ≥ªÊñπÂºè',
          icon: 'none'
        })
        return
      }

      uni.makePhoneCall({
        phoneNumber: this.riderInfo.phone
      })
    },

    /**
     * ÂèñÊ∂àËÆ¢Âçï
     */
    cancelOrder() {
      uni.showModal({
        title: 'ÂèñÊ∂àËÆ¢Âçï',
        content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àËøô‰∏™ËÆ¢ÂçïÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'ÂèñÊ∂à‰∏≠...' })

              const result = await cancelOrder({
                orderId: this.orderId,
                cancelReason: 'Áî®Êà∑‰∏ªÂä®ÂèñÊ∂à'
              })

              uni.hideLoading()

              if (result.code === 200) {
                uni.showToast({
                  title: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à',
                  icon: 'success'
                })
                // Âà∑Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ
                this.loadOrderDetail()
              } else {
                uni.showToast({
                  title: result.message || 'ÂèñÊ∂àÂ§±Ë¥•',
                  icon: 'none'
                })
              }
            } catch (error) {
              uni.hideLoading()
              console.error('‚ùå ÂèñÊ∂àËÆ¢ÂçïÂ§±Ë¥•:', error)
              uni.showToast({
                title: 'ÂèñÊ∂àÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
                icon: 'none'
              })
            }
          }
        }
      })
    },

    /**
     * ÂéªÊîØ‰ªò
     */
    goToPay() {
      uni.navigateTo({
        url: `/pages/order/payment?orderId=${this.orderId}&totalAmount=${this.orderInfo.totalAmount}`
      })
    },

    /**
     * ÂéªËØÑ‰ª∑
     */
    goToEvaluate() {
      uni.navigateTo({
        url: `/pages/evaluation/create?orderId=${this.orderId}`
      })
    },

    /**
     * Êü•ÁúãËØÑ‰ª∑
     */
    viewEvaluation() {
      uni.navigateTo({
        url: `/pages/evaluation/detail?orderId=${this.orderId}`
      })
    },

    /**
     * Êõ¥Â§öËèúÂçï
     */
    showMoreMenu() {
      uni.showActionSheet({
        itemList: ['Â§çÂà∂ËÆ¢ÂçïÂè∑', 'ËÅîÁ≥ªÂÆ¢Êúç'],
        success: (res) => {
          if (res.tapIndex === 0) {
            this.copyOrderNo()
          } else if (res.tapIndex === 1) {
            uni.showToast({
              title: 'ÂÆ¢ÊúçÂäüËÉΩÂºÄÂèë‰∏≠',
              icon: 'none'
            })
          }
        }
      })
    },

    /**
     * ËøîÂõû‰∏ä‰∏ÄÈ°µ
     */
    goBack() {
      uni.navigateBack()
    },

    /**
     * Âà†Èô§ËÆ¢Âçï
     */
    deleteOrder() {
      uni.showModal({
        title: 'Âà†Èô§ËÆ¢Âçï',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ËÆ¢ÂçïÂêóÔºüÂà†Èô§ÂêéÂ∞ÜÊó†Ê≥ïÊÅ¢Â§ç',
        success: (res) => {
          if (res.confirm) {
            uni.showToast({
              title: 'Âà†Èô§ÂäüËÉΩÂºÄÂèë‰∏≠',
              icon: 'none'
            })
            // TODO: Ë∞ÉÁî®Âà†Èô§ËÆ¢ÂçïAPI
            // const res = await deleteOrderApi(this.orderId)
          }
        }
      })
    },

    /**
     * ÂÜçÊù•‰∏ÄÂçï
     */
    reorder() {
      if (!this.orderInfo) return

      // Ë∑≥ËΩ¨Âà∞ÂàõÂª∫ËÆ¢ÂçïÈ°µÈù¢ÔºåÊê∫Â∏¶ËÆ¢Âçï‰ø°ÊÅØ
      uni.navigateTo({
        url: `/pages/order/create?orderId=${this.orderId}`
      })
    },

    /**
     * ÂêØÂä®ÂÄíËÆ°Êó∂
     */
    startCountdown(seconds) {
      this.countdown = seconds
      this.stopCountdown() // ÂÖàÂÅúÊ≠¢Â∑≤ÊúâÁöÑÂÆöÊó∂Âô®
      
      this.countdownTimer = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown--
        } else {
          this.stopCountdown()
          // ËÆ¢ÂçïÂ∑≤Ë∂ÖÊó∂ÔºåÂà∑Êñ∞ËÆ¢ÂçïËØ¶ÊÉÖËé∑ÂèñÊúÄÊñ∞Áä∂ÊÄÅ
          this.loadOrderDetail()
        }
      }, 1000)
    },

    /**
     * ÂÅúÊ≠¢ÂÄíËÆ°Êó∂
     */
    stopCountdown() {
      if (this.countdownTimer) {
        clearInterval(this.countdownTimer)
        this.countdownTimer = null
      }
    },

    /**
     * Ê†ºÂºèÂåñÂÄíËÆ°Êó∂ÊòæÁ§∫
     */
    formatCountdown(seconds) {
      const minutes = Math.floor(seconds / 60)
      const secs = seconds % 60
      return `${String(minutes).padStart(2, '0')}:${String(secs).padStart(2, '0')}`
    },

    /**
     * ÂàùÂßãÂåñÂú∞ÂõæÊ†áËÆ∞
     */
    initMapMarkers() {
      if (!this.orderInfo || !this.orderInfo.pickupLat) return

      const markers = [
        {
          id: 1,
          latitude: this.orderInfo.pickupLat,
          longitude: this.orderInfo.pickupLng,
          title: 'Âèñ‰ª∂ÁÇπ',
          iconPath: '/static/icons/marker_pickup.png',
          width: 32,
          height: 32,
          anchor: { x: 0.5, y: 1 },
          label: { content: 'Âèñ', color: '#667eea', bgColor: '#fff', padding: 4, borderRadius: 10, fontSize: 10 },
          callout: {
            content: this.orderInfo.pickupAddr || 'ÂèñË¥ßÂú∞',
            color: '#333',
            fontSize: 12,
            borderRadius: 4,
            bgColor: '#fff',
            padding: 6,
            display: 'ALWAYS'
          }
        },
        {
          id: 2,
          latitude: this.orderInfo.deliveryLat,
          longitude: this.orderInfo.deliveryLng,
          title: 'ÈÄÅËææÁÇπ',
          iconPath: '/static/icons/marker_delivery.png',
          width: 32,
          height: 32,
          anchor: { x: 0.5, y: 1 },
          label: { content: 'Êî∂', color: '#ff4d4f', bgColor: '#fff', padding: 4, borderRadius: 10, fontSize: 10 },
          callout: {
            content: this.orderInfo.deliveryAddr || 'ÈÄÅË¥ßÂú∞',
            color: '#333',
            fontSize: 12,
            borderRadius: 4,
            bgColor: '#fff',
            padding: 6,
            display: 'ALWAYS'
          }
        }
      ]

      this.markers = markers
      this.mapCenter = {
        latitude: (this.orderInfo.pickupLat + this.orderInfo.deliveryLat) / 2,
        longitude: (this.orderInfo.pickupLng + this.orderInfo.deliveryLng) / 2
      }

      console.log('üó∫Ô∏è [DETAIL] Âú∞ÂõæÊ†áÊ≥®ÂàùÂßãÂåñ:', {
        markersCount: markers.length,
        pickup: [this.orderInfo.pickupLat, this.orderInfo.pickupLng],
        delivery: [this.orderInfo.deliveryLat, this.orderInfo.deliveryLng],
        center: this.mapCenter
      })

      // ÁîªÂá∫Ëµ∑ÁªàÁÇπËøûÁ∫ø
      this.polyline = [{
        points: [
          { latitude: this.orderInfo.pickupLat, longitude: this.orderInfo.pickupLng },
          { latitude: this.orderInfo.deliveryLat, longitude: this.orderInfo.deliveryLng }
        ],
        color: '#667eea',
        width: 4,
        dottedLine: true
      }]

      // Âº∫Âà∂Ëß¶Âèë includePoints
      this.$nextTick(() => {
        const mapCtx = uni.createMapContext('orderMap', this)
        mapCtx.includePoints({
          padding: [50, 50, 50, 50],
          points: this.markers
        })
      })
    },

    /**
     * Â§ÑÁêÜ‰ΩçÁΩÆËøΩË∏™ÈÄªËæë
     */
    handleTracking() {
      this.stopTracking()

      const user = getUserInfo()
      const currentUserId = user ? user.id : null
      
      console.log('üßê [DETAIL] ËøΩË∏™ÊùÉÈôêÊ£ÄÊü•:', {
        orderId: this.orderId,
        runnerId: this.orderInfo.runnerId,
        userId: this.orderInfo.userId,
        currentUserId: currentUserId,
        status: this.orderStatus
      })

      if (this.orderInfo.runnerId && this.orderInfo.runnerId == currentUserId) {
        console.log('üèÅ ÂΩìÂâçÁî®Êà∑ÊòØÈ™ëÊâãÔºåÂºÄÂêØËøΩË∏™ÂíåÊãâÂèñ')
        // ÊàëÊòØÈ™ëÊâã -> ÂºÄÂêØÂÖ®Â±Ä‰ΩçÁΩÆËøΩË∏™ÔºàÂç≥‰ΩøÁ¶ªÂºÄËØ¶ÊÉÖÈ°µ‰πü‰ºöÁªßÁª≠‰∏äÊä•Ôºâ
        riderTracker.checkAndStart();
        // ÂêåÊó∂‰πüÊãâÂèñ‰ΩçÁΩÆÔºå‰ª•‰æøÂú®Ê≠§ÊòæÁ§∫Ëá™Â∑±ÁöÑÂõæÊ†á
        this.startUserPolling()
      } else if (this.orderInfo.userId && this.orderInfo.userId == currentUserId) {
        console.log('üèÅ ÂΩìÂâçÁî®Êà∑ÊòØÂÆ¢ÔºåÂºÄÂêØÊãâÂèñ')
        // ÊàëÊòØÂèëÂçï‰∫∫ -> Âè™Ë¶ÅÊòØÈÖçÈÄÅ‰∏≠(2)ÊàñÂ∑≤ÂÆåÊàê(3)Â∞±Â∞ùËØïÊãâÂèñ‰ΩçÁΩÆ
        if (this.orderStatus === 2 || this.orderStatus === 3) {
          this.startUserPolling()
        } else {
          console.log('‚è≠Ô∏è ËÆ¢ÂçïÈùûÈÖçÈÄÅ‰∏≠/Â∑≤ÂÆåÊàêÁä∂ÊÄÅÔºåË∑≥ËøáÊãâÂèñ')
        }
      } else {
        console.log('üö∑ Êó†ÊùÉÈôêÂºÄÂêØ‰ΩçÁΩÆËøΩË∏™')
      }
    },
    /**
     * Áî®Êà∑Á´ØÔºöÊãâÂèñ‰ΩçÁΩÆ
     */
    startUserPolling() {
      console.log('üëÄ Áî®Êà∑Á´ØÔºöÂºÄÂêØËΩ®ËøπÊãâÂèñÂÆöÊó∂Âô®')
      
      const doPoll = async () => {
        try {
          const res = await getRiderLocation(this.orderId)
          if (res.code === 200 && res.data) {
            console.log('üèéÔ∏è [POLL] Êî∂Âà∞È™ëÊâã‰ΩçÁΩÆÊï∞ÊçÆ:', JSON.stringify(res.data))
            this.updateRiderMarker(res.data.latitude, res.data.longitude)
          } else {
            console.log('üèéÔ∏è [POLL] Êé•Âè£ËøîÂõûÁ©∫ÊàñÂ§±Ë¥•:', res)
          }
        } catch (err) {
          console.error('‚ùå ÊãâÂèñËΩ®ËøπÂ§±Ë¥•:', err)
        }
      }

      doPoll()
      this.trackingTimer = setInterval(doPoll, 15000) // 15Áßí‰∏ÄÊ¨°
    },

    updateRiderMarker(lat, lng) {
      if (!lat || !lng) {
        console.warn('‚ö†Ô∏è updateRiderMarker: ÂùêÊ†áÊó†Êïà', lat, lng)
        return
      }
      
      const riderMarkerId = 999
      const latNum = Number(lat)
      const lngNum = Number(lng)
      
      console.log('üìç [DETAIL] Êõ¥Êñ∞È™ëÊâãÊ†áÁÇπ:', latNum, lngNum)
      
      const existingIndex = this.markers.findIndex(m => m.id === riderMarkerId)
      
      const riderMarker = {
        id: riderMarkerId,
        latitude: latNum,
        longitude: lngNum,
        title: 'È™ëÊâã‰ΩçÁΩÆ',
        iconPath: '/static/icons/marker_rider.png',
        width: 36,
        height: 36,
        zIndex: 999,
        // Â¶ÇÊûú‰∏äÈù¢ÈÉΩ‰∏çÊòæÁ§∫ÔºåÂ∞ùËØï‰∏çÂä†Ëøô‰∫õÂ§çÊùÇÈÖçÁΩÆÁúãÁúã
        anchor: { x: 0.5, y: 0.5 },
        // Ê∑ªÂä†ÊñáÂ≠óÊ†áÁ≠æÔºåÈò≤Ê≠¢ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•Êó∂Áúã‰∏çËßÅ
        label: {
          content: 'ÈÖçÈÄÅ‰∏≠',
          color: '#ffffff',
          fontSize: 10,
          bgColor: '#07c160',
          padding: 3,
          borderRadius: 5,
          anchorX: 0,
          anchorY: -40
        }
      }

      if (existingIndex > -1) {
        this.$set(this.markers, existingIndex, riderMarker)
        console.log('‚úÖ Â∑≤‰ΩøÁî® $set Êõ¥Êñ∞Áé∞ÊúâÈ™ëÊâãÊ†áÁÇπ')
      } else {
        this.markers = [...this.markers, riderMarker]
        console.log('‚úÖ Â∑≤‰ΩøÁî®Ëß£ÊûÑËµãÂÄºÊñ∞Â¢ûÈ™ëÊâãÊ†áÁÇπÔºåÂΩìÂâçÊÄªÊ†áÁÇπÊï∞:', this.markers.length)
      }
      
      console.log('üîç ÂΩìÂâçÊâÄÊúâÊ†áËÆ∞ËØ¶ÊÉÖ (‰ªÖÁªèÁ∫¨Â∫¶):', this.markers.map(m => ({ id: m.id, lat: m.latitude, lng: m.longitude })))

      // ÂÆûÊó∂Ë∞ÉÊï¥ËßÜÈáé‰ª•ÂåÖÂê´È™ëÊâã
      this.$nextTick(() => {
        const mapCtx = uni.createMapContext('orderMap', this)
        mapCtx.includePoints({
          padding: [80, 80, 80, 80],
          points: this.markers
        })
      })
    },

    /**
     * ÂÅúÊ≠¢ËøΩË∏™
     */
    stopTracking() {
      if (this.trackingTimer) {
        clearInterval(this.trackingTimer)
        this.trackingTimer = null
        console.log('‚èπÔ∏è ‰ΩçÁΩÆËøΩË∏™Â∑≤ÂÅúÊ≠¢')
      }
    }
  }
}
</script>

